$sm: 576px;
$md: 768px;
$lg: 992px;

@mixin for-small-up {
  @media screen and (min-width: $sm) {
    @content;
  }
}

@mixin for-medium-up {
  @media screen and (min-width: $md) {
    @content;
  }
}

@mixin for-large-up {
  @media screen and (min-width: $lg) {
    @content;
  }
}
